@font-face {
  font-family: "Pretendard-Regular";
  src: url("https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff")
    format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "PyeongChangPeace-Bold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2206-02@1.0/PyeongChangPeace-Bold.woff2")
    format("woff2");
  font-weight: 700;
  font-style: normal;
}

div {
  overflow: hidden;
}

div#OXButton {
  display: inline-block;
  font-size: 250%;
  padding: 10%;
  margin-left: 5%;
  margin-right: 5%;
  border: 0.25px solid transparent;
  box-shadow: 0 1px 2.5px 1px rgb(67 67 67 / 20%);
  border-radius: 25px;
}

div#OXButton:hover,
div#OXButton:focus,
div#OXButton:active {
  background-color: gray;
}

div#titleCntDiv {
  min-width: 70%;
  max-width: 70%;
  max-height: 30%;
  min-height: 30%;
  display: inline-block;
  line-break: strict;
}

img#imgIcon {
  display: block;
  margin: auto;
  width: 40px;
  margin-bottom: 8%;
}

img#resultImgIcon {
  display: block;
  margin: auto;
  width: 70px;
}

#desc {
  font-family: "Pretendard-Regular";
  font-weight: 100;
  letter-spacing: -2px;
  line-height: 133%;
}

#desc-small {
  font-family: "Pretendard-Regular";
  font-weight: 100;
  font-size: 90%;
  letter-spacing: -1.8px;
  line-height: 133%;
}

h3#waitHeader {
  margin-top: -10%;
  margin-bottom: -3%;
  padding-top: 3%;
  padding-bottom: 3%;
  border: 0.25px solid transparent;
  box-shadow: 0 1px 2.5px 1px rgb(67 67 67 / 20%);
  border-radius: 25px;
}
/*
div :not(.App) {
  border: 1px red solid;
}*/

div #titleStartButton {
  width: 70%;
  font-family: "Pretendard-Regular";
  font-size: 150%;
  letter-spacing: -0.8px;
  margin: auto;
  margin-top: 10%;
  margin-bottom: 5%;
  background-color: white;
  border: 0.25px solid transparent;
  box-shadow: 0 1px 2.5px 1px rgb(67 67 67 / 20%);
  border-radius: 25px;
  padding-top: 2.5%;
  padding-bottom: 2.5%;
}

div #titleStartButton:hover,
div #titleStartButton:focus,
div #titleStartButton:active {
  background-color: gray;
}

div#titleCntButton {
  padding-top: 10%;
}

span#titleCnt {
  min-width: 70%;
  max-width: 70%;
  font-size: xx-large;
}

h1#titleText {
  font-size: 450%;
  line-height: 95%;
  padding: 0px 30px 0px 30px;
  margin-top: 10%;
  margin-bottom: 10%;
}

div#container {
  width: 55%;
  scale: 140%;
  margin: 5%;
  padding: 5%;
  padding-top: 15%;
  padding-bottom: 15%;
  font-family: "PyeongChangPeace-Bold";
  font-weight: bolder;
  display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: white;
  border-radius: 25px;
  border: 1px black solid;
}

body {
  margin: 0px;
  height: 100%;
}

div.App {
  position: absolute;
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: black;
}
